@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing-container{position:absolute;left:0;top:0;background:url(/react-bookworms/static/media/landing-bg.e5bea38e.jpg) no-repeat center center fixed;background-size:cover;height:100vh;width:100vw;overflow:hidden}.title{color:#fff;font-weight:bold;font-size:5em}.welcome-container{background-color:#000;opacity:0.8;position:absolute;top:10%;left:0;padding:15px;-webkit-animation-name:welcome-entering;animation-name:welcome-entering;-webkit-animation-duration:2s;animation-duration:2s}@-webkit-keyframes welcome-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:10%}}@keyframes welcome-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:10%}}.floating-nav{background-color:#000;opacity:0.8;position:absolute;right:0;top:20%;max-width:25vw;color:white;padding:15px;-webkit-animation-name:floating-entering;animation-name:floating-entering;-webkit-animation-duration:2s;animation-duration:2s}@-webkit-keyframes floating-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:20%}}@keyframes floating-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:20%}}.link{color:white;font-size:1.5em;text-decoration:underline}.link:hover{color:white;font-weight:bold}.footer{position:absolute;bottom:0;left:0;right:0;margin-left:auto;margin-right:auto;color:white;font-weight:bold;background-color:black;opacity:0.7;padding-top:5px}.footer a{color:white;text-decoration:none}@media (max-width: 1300px){.title{font-size:4em}.floating-nav{max-width:35vw;top:25%}@-webkit-keyframes floating-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:25%}}@keyframes floating-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:25%}}}@media (max-width: 768px){.welcome-container{top:5%}.welcome-container .title{font-size:3em}@-webkit-keyframes welcome-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:5%}}@keyframes welcome-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:5%}}.floating-nav{max-width:100vw;top:30%}@-webkit-keyframes floating-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:30%}}@keyframes floating-entering{0%{opacity:0.2;top:0px}100%{opacity:0.8;top:30%}}}

.main-nav {
    margin-bottom: 2em;
}
.books-container{left:0;right:0;margin-left:auto;margin-right:auto;text-align:center;display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));grid-gap:10px}.books-container .img-container{height:240px;width:150px;margin-top:10px;margin-left:auto;margin-right:auto}.books-container .book-card{display:flex;flex-direction:column;margin-bottom:10px;padding-top:10px}.books-container .book-card .book-info-container{min-height:400px;margin-bottom:2em}.books-container .book-card .book-info-container .book-name{font-weight:bold;font-size:1.5em}.books-container .book-card .book-info-container .book-icon{font-size:5em;margin-top:1em}.books-container .book-card .book-info-container .author{margin-top:1em}.books-container .book-card .notes-container{position:absolute;bottom:10px;left:0;right:0;margin-left:auto;margin-right:auto;color:white}.books-container .book-card .notes-container .view-notes{margin-top:1em}.books-container .book-card .notes-container .view-notes:focus{border:none}.add-new-container{display:flex;flex-direction:column;justify-content:center;align-items:center}

body{font-family:"Raleway", sans-serif !important}.pointer{cursor:pointer}.btn-action{background-color:#d45d79 !important;border-color:#d45d79 !important;color:white !important}.btn-action:hover,.btn-action:focus .btn-action:active{border-color:#d45d79 !important;color:white !important}.required-field::after{content:'*';font-weight:bold}

